import React, { Component } from 'react';
import Link from './Link';

export default class Menu extends Component {
	
	shouldComponentUpdate({ classes, page }) {
		const curProps = this.props;
		return classes !== curProps.classes ||
					 page !== curProps.page;
	}
	
	render() {
		const { classes, page, nav } = this.props;
		return (
			<section className={classes}>
{/*				<div className="panel panel1"></div>
				<div className="panel panel2"></div>*/}
				<div className="arrow"></div>
				<div className='links'>
					<Link 
						menuClasses={classes}
						page={page}
						pageTo="Home"
						nav={nav} />
					<Link 
						menuClasses={classes}
						page={page}
						pageTo="Work"
						nav={nav} />
					<Link 
						menuClasses={classes}
						page={page}
						pageTo="Contact"
						nav={nav} />
				</div>
			</section>
		);
	}
}


